      @include media-breakpoint-down(sm) {
        padding: 0 1.25rem;
      }
    }

    .navbar-left {
      display: flex;
      align-items: center;
      gap: 1rem;

      @include media-breakpoint-down(xs-1) {
        gap: 0.5rem;
      }

      .navbar-brand {
  
        .navbar-brand__text {
          font-weight: 700;
          font-size: 1.5625rem;
          color: $white;

          @include media-breakpoint-down(lg) {
            display: none;
          }
        }

        .navbar-brand__icon {
          display: none;
          width: auto;
          height: auto;
          max-width: 2rem;
          max-height: 2rem;

          @include media-breakpoint-down(lg) {
            display: block;
          }
        }
      }
    }
    .navbar-right {
      display: flex;
      align-items: center;
      gap: 2.75rem;

      @include media-breakpoint-down(xl) {
        gap: 1.5rem;
      }

      @include media-breakpoint-down(lg) {
        gap: 1.125rem;
      }

      @include media-breakpoint-down(xs-1) {
        gap: 0.5rem;
      }

