        }
      }

      .navbar-profile-menu {
        display: flex;
        flex-wrap: wrap;
        row-gap: 1rem;
        align-items: center;
        cursor: pointer;
        position: relative;
        .profile-photo-icon{
          @include media-breakpoint-down(md) {
            width: toRem(25.26);
            height: toRem(25.26);
          }
        }
