        border: 1px solid transparent;
        text-indent: 1.5rem;
        transition: all linear .2s;

        &::placeholder {
          color: $grey-60;
          font-size: 1rem;
          font-weight: 400;
          line-height: 1.5rem;
        }

        &:active,&:focus,&:focus-visible,&:focus-within{
          border-color: $ultramarine-blue;
          border-width: 2px;
        }
      }

      .navbar-search-popup__image {
        position: absolute;
        left: 2rem;

        &--cancel {
          position: absolute;
          right: 2rem;
          transform: rotate(45deg);
          cursor: pointer;
        }
      }
    }
  }
}